/**
 * LayoutManager - 
 * 
 */

class LayoutManager {
  constructor(width, height) {
    this.width = width;
    this.height = height;
    this.centerX = width / 2;
    this.centerY = height / 2;
  }

  resolveNodeLabel(node = {}, options = {}) {
    const includeSense = options?.includeSense !== false;
    const title = typeof node?.name === 'string' ? node.name.trim() : '';
    const senseTitle = typeof node?.activeSenseTitle === 'string'
      ? node.activeSenseTitle.trim()
      : '';
    if (includeSense && title && senseTitle) {
      return `${title}\n${senseTitle}`;
    }
    return title || '';
  }

  resize(width, height) {
    this.width = width;
    this.height = height;
    this.centerX = width / 2;
    this.centerY = height / 2;
  }

  /**
   * 
   * 
   */
  calculateHomeLayout(rootNodes, featuredNodes, searchResults = []) {
    const layout = {
      nodes: [],
      lines: []
    };

    const isSearching = searchResults.length > 0;

    if (isSearching) {
      // 
      // HTMLWebGL
      // React
      return layout;
    }

    // 

    // 
    const yOffset = 70;

    // 
    const rootStartY = this.height * 0.35 + yOffset;
    const rootCols = Math.min(3, rootNodes.length);
    const rootSpacingX = Math.min(250, this.width * 0.25);
    const rootSpacingY = 180;

    rootNodes.forEach((node, index) => {
      const row = Math.floor(index / rootCols);
      const col = index % rootCols;
      const rowWidth = Math.min(rootNodes.length - row * rootCols, rootCols);
      const offsetX = (rowWidth - 1) * rootSpacingX / 2;

      layout.nodes.push({
        id: `root-${node._id}`,
        x: this.centerX - offsetX + col * rootSpacingX,
        y: rootStartY + row * rootSpacingY,
        radius: 70,
        scale: 1,
        opacity: 1,
        type: 'root',
        label: this.resolveNodeLabel(node, { includeSense: false }),
        visualStyle: node.visualStyle || null,
        labelColor: node.visualStyle?.textColor || '',
        data: node,
        visible: true
      });
    });

    // 
    const featuredY = this.height * 0.70 + yOffset;
    const featuredSpacing = 150;
    const featuredStartX = this.centerX - (featuredNodes.length - 1) * featuredSpacing / 2;

    featuredNodes.forEach((node, index) => {
      layout.nodes.push({
        id: `featured-${node._id}`,
        x: featuredStartX + index * featuredSpacing,
        y: featuredY,
        radius: 55,
        scale: 1,
        opacity: 1,
        type: 'featured',
        label: this.resolveNodeLabel(node, { includeSense: false }),
        visualStyle: node.visualStyle || null,
        labelColor: node.visualStyle?.textColor || '',
        data: node,
        visible: true
      });
    });

    return layout;
  }

  /**
   * 
   *  + () + ()
   */
  calculateNodeDetailLayout(centerNode, parentNodes = [], childNodes = []) {
    const layout = {
      nodes: [],
      lines: []
    };

    // 
    const yOffset = 60;

    // 
    layout.nodes.push({
      id: `center-${centerNode._id}`,
      x: this.centerX,
      y: this.centerY + yOffset,
      radius: 80,
      scale: 1,
      opacity: 1,
      type: 'center',
      label: this.resolveNodeLabel(centerNode),
      visualStyle: centerNode.visualStyle || null,
      labelColor: centerNode.visualStyle?.textColor || '',
      data: centerNode,
      visible: true
    });

    //  ()
    const parentDistance = Math.min(250, this.height * 0.25);
    const parentRadius = 50;

    parentNodes.forEach((node, index) => {
      const angle = Math.PI + (Math.PI / (parentNodes.length + 1)) * (index + 1);
      const x = this.centerX + Math.cos(angle) * parentDistance;
      const y = this.centerY + yOffset + Math.sin(angle) * parentDistance;

      const nodeId = `parent-${node._id}`;

      layout.nodes.push({
        id: nodeId,
        x,
        y,
        radius: parentRadius,
        scale: 1,
        opacity: 1,
        type: 'parent',
        label: this.resolveNodeLabel(node),
        visualStyle: node.visualStyle || null,
        labelColor: node.visualStyle?.textColor || '',
        data: node,
        visible: true
      });

      // 
      layout.lines.push({
        from: `center-${centerNode._id}`,
        to: nodeId,
        color: [0.06, 0.73, 0.51, 0.6] // 
      });
    });

    //  ()
    const childDistance = Math.min(230, this.height * 0.22);
    const childRadius = 40;

    childNodes.forEach((node, index) => {
      const angle = (Math.PI / (childNodes.length + 1)) * (index + 1);
      const x = this.centerX + Math.cos(angle) * childDistance;
      const y = this.centerY + yOffset + Math.sin(angle) * childDistance;

      const nodeId = `child-${node._id}`;

      layout.nodes.push({
        id: nodeId,
        x,
        y,
        radius: childRadius,
        scale: 1,
        opacity: 1,
        type: 'child',
        label: this.resolveNodeLabel(node),
        visualStyle: node.visualStyle || null,
        labelColor: node.visualStyle?.textColor || '',
        data: node,
        visible: true
      });

      // 
      layout.lines.push({
        from: `center-${centerNode._id}`,
        to: nodeId,
        color: [0.98, 0.75, 0.14, 0.6] // 
      });
    });

    return layout;
  }

  /**
   * 
   *  +  + 
   */
  calculateTitleDetailLayout(centerNode, graphNodes = [], graphEdges = [], levelByNodeId = {}) {
    const layout = {
      nodes: [],
      lines: []
    };
    if (!centerNode?._id) return layout;

    const yOffset = 60;
    const centerId = String(centerNode._id);
    const centerLayoutId = `center-${centerId}`;
    const ringNodes = (Array.isArray(graphNodes) ? graphNodes : [])
      .filter((item) => String(item?._id || '') !== centerId);
    const nodeIdToLayoutId = new Map([[centerId, centerLayoutId]]);

    layout.nodes.push({
      id: centerLayoutId,
      x: this.centerX,
      y: this.centerY + yOffset,
      radius: 82,
      scale: 1,
      opacity: 1,
      type: 'center',
      label: this.resolveNodeLabel(centerNode, { includeSense: false }),
      visualStyle: centerNode.visualStyle || null,
      labelColor: centerNode.visualStyle?.textColor || '',
      data: {
        ...centerNode,
        graphLevel: 0
      },
      visible: true
    });

    const groups = new Map();
    ringNodes.forEach((node) => {
      const nodeId = String(node?._id || '');
      if (!nodeId) return;
      const rawLevel = Number(levelByNodeId?.[nodeId]);
      const level = Number.isFinite(rawLevel) && rawLevel > 0 ? Math.floor(rawLevel) : 1;
      const group = groups.get(level) || [];
      group.push(node);
      groups.set(level, group);
    });

    const levels = Array.from(groups.keys()).sort((a, b) => a - b);
    const minSide = Math.min(this.width, this.height);
    const baseDistance = Math.max(190, Math.min(minSide * 0.28, 320));
    const ringGap = Math.max(130, Math.min(minSide * 0.18, 210));
    const minNodeGap = Math.max(26, Math.min(minSide * 0.045, 44));
    const centerRadius = 82;
    let previousRingDistance = 0;
    let previousRingNodeRadius = centerRadius;

    levels.forEach((level) => {
      const nodes = (groups.get(level) || []).slice();
      nodes.sort((a, b) => (a?.name || '').localeCompare((b?.name || ''), 'zh-Hans-CN'));
      const count = Math.max(1, nodes.length);
      const startAngle = -Math.PI / 2;
      const baseNodeRadius = Math.max(30, 54 - Math.min(3, level - 1) * 6);
      const preferredDistance = baseDistance + (level - 1) * ringGap;
      const maxRadiusByPreferredDistance = count > 1
        ? ((Math.PI * 2 * preferredDistance) / count - minNodeGap) / 2
        : baseNodeRadius;
      const nodeRadius = Math.max(22, Math.min(baseNodeRadius, maxRadiusByPreferredDistance));
      const distanceByArc = count > 1
        ? (count * ((nodeRadius * 2) + minNodeGap)) / (Math.PI * 2)
        : 0;
      const distanceByRingGap = previousRingDistance > 0
        ? previousRingDistance + previousRingNodeRadius + nodeRadius + minNodeGap
        : centerRadius + nodeRadius + minNodeGap;
      const distance = Math.max(
        preferredDistance,
        distanceByArc,
        distanceByRingGap
      );
      previousRingDistance = distance;
      previousRingNodeRadius = nodeRadius;

      nodes.forEach((node, index) => {
        const angle = startAngle + ((Math.PI * 2) * index) / count;
        const nodeId = String(node?._id || '');
        const layoutId = `title-${nodeId}`;
        nodeIdToLayoutId.set(nodeId, layoutId);
        layout.nodes.push({
          id: layoutId,
          x: this.centerX + Math.cos(angle) * distance,
          y: this.centerY + yOffset + Math.sin(angle) * distance,
          radius: nodeRadius,
          scale: 1,
          opacity: 1,
          type: 'title',
          label: this.resolveNodeLabel(node, { includeSense: false }),
          visualStyle: node.visualStyle || null,
          labelColor: node.visualStyle?.textColor || '',
          data: {
            ...node,
            graphLevel: level
          },
          visible: true
        });
      });
    });

    (Array.isArray(graphEdges) ? graphEdges : []).forEach((edge) => {
      const nodeAId = String(edge?.nodeAId || '');
      const nodeBId = String(edge?.nodeBId || '');
      const isCenterEdge = nodeAId === centerId || nodeBId === centerId;
      if (!isCenterEdge) return;
      const from = nodeIdToLayoutId.get(nodeAId);
      const to = nodeIdToLayoutId.get(nodeBId);
      if (!from || !to) return;

      layout.lines.push({
        id: edge?.edgeId || `${nodeAId}|${nodeBId}`,
        from,
        to,
        color: isCenterEdge ? [0.72, 0.86, 0.99, 0.74] : [0.55, 0.72, 0.92, 0.54],
        clickable: true,
        edgeMeta: edge
      });
    });

    return layout;
  }

  /**
   * 
   * 
   */
  calculateTransition(currentLayout, newLayout) {
    const transitions = {
      enter: [],      //  ( + )
      exit: [],       //  ( + )
      move: [],       //  ()
      stay: []        // 
    };

    const currentIds = new Set(currentLayout.nodes.map(n => n.id));
    const newIds = new Set(newLayout.nodes.map(n => n.id));

    // 
    for (const node of currentLayout.nodes) {
      if (!newIds.has(node.id)) {
        transitions.exit.push({
          id: node.id,
          from: { ...node },
          to: { ...node, scale: 0, opacity: 0 }
        });
      }
    }

    // 
    for (const node of newLayout.nodes) {
      if (!currentIds.has(node.id)) {
        // +
        transitions.enter.push({
          id: node.id,
          from: { ...node, scale: 0, opacity: 0 },
          to: { ...node }
        });
      } else {
        // 
        const currentNode = currentLayout.nodes.find(n => n.id === node.id);
        const styleChanged = JSON.stringify(currentNode.visualStyle || null) !== JSON.stringify(node.visualStyle || null)
          || (currentNode.labelColor || '') !== (node.labelColor || '')
          || (currentNode.label || '') !== (node.label || '');
        const moved = currentNode.x !== node.x || currentNode.y !== node.y ||
                     currentNode.scale !== node.scale || currentNode.radius !== node.radius ||
                     styleChanged;

        if (moved) {
          transitions.move.push({
            id: node.id,
            from: { ...currentNode },
            to: { ...node }
          });
        } else {
          transitions.stay.push({
            id: node.id,
            config: { ...node }
          });
        }
      }
    }

    return transitions;
  }

  /**
   * 
   * 
   */
  calculateClickTransition(clickedNode, fromLayout, toLayout) {
    // 
    const sourceNode = fromLayout.nodes.find(n => n.id === clickedNode.id);
    const targetCenterNode = toLayout.nodes.find(n => n.type === 'center');

    if (!sourceNode || !targetCenterNode) {
      return this.calculateTransition(fromLayout, toLayout);
    }

    const transitions = {
      enter: [],
      exit: [],
      move: [],
      stay: [],
      special: null  // 
    };

    // 
    transitions.special = {
      id: sourceNode.id,
      from: { ...sourceNode },
      to: {
        ...targetCenterNode,
        id: sourceNode.id  // ID
      }
    };

    // 
    for (const node of fromLayout.nodes) {
      if (node.id !== sourceNode.id) {
        transitions.exit.push({
          id: node.id,
          from: { ...node },
          to: { ...node, opacity: 0, scale: node.scale * 0.5 }
        });
      }
    }

    // 
    for (const node of toLayout.nodes) {
      if (node.id !== targetCenterNode.id) {
        transitions.enter.push({
          id: node.id,
          from: {
            ...node,
            x: targetCenterNode.x + (Math.random() - 0.5) * 100,
            y: targetCenterNode.y + (Math.random() - 0.5) * 100,
            scale: 0,
            opacity: 0
          },
          to: { ...node }
        });
      }
    }

    return transitions;
  }

  // ====================  ====================

  /**
   * 
   * @param {Object} newNodeData -  {name, description}
   * @param {Object} nodeA -  parentNodesInfo, childNodesInfo
   * @param {string} relationType - 'extends' | 'contains' | 'insert'
   * @param {Object} nodeB -  insert  node  direction
   * @param {Object} currentLayout - 
   * @returns {Object} 
   */
  calculateAssociationPreviewLayout(newNodeData, nodeA, relationType, nodeB, currentLayout) {
    // 
    const yOffset = 60;

    //  nodeA 
    const nodeAInLayout = currentLayout.nodes.find(
      n => n.data && n.data._id === nodeA._id
    );

    //  nodeA
    const nodeAPosition = nodeAInLayout
      ? { x: nodeAInLayout.x, y: nodeAInLayout.y }
      : { x: this.centerX, y: this.centerY + yOffset };

    // 
    if (relationType === 'insert' && nodeB) {
      return this.calculateInsertPreviewLayout(newNodeData, nodeA, nodeB, currentLayout, nodeAPosition);
    } else {
      return this.calculateSimplePreviewLayout(newNodeData, nodeA, relationType, currentLayout, nodeAPosition);
    }
  }

  /**
   *  nodeA 
   */
  calculateSimplePreviewLayout(newNodeData, nodeA, relationType, currentLayout, nodeAPosition) {
    const result = {
      movements: [],
      previewNode: null,
      previewLines: []
    };

    const distance = 150;
    const previewRadius = 45;

    // 
    let previewX, previewY;
    let lineColor;

    if (relationType === 'extends') {
      //  nodeA 
      //  nodeA 
      const existingParentCount = nodeA.parentNodesInfo?.length || 0;
      const angle = Math.PI + (Math.PI / (existingParentCount + 2)) * (existingParentCount + 1);

      previewX = nodeAPosition.x + Math.cos(angle) * distance;
      previewY = nodeAPosition.y + Math.sin(angle) * distance;
      lineColor = [0.06, 0.73, 0.51, 0.8]; // 

      // 
      if (existingParentCount > 0 && currentLayout.nodes) {
        const parentNodes = currentLayout.nodes.filter(n => n.type === 'parent');
        const totalParents = existingParentCount + 1;

        parentNodes.forEach((node, index) => {
          const newAngle = Math.PI + (Math.PI / (totalParents + 1)) * (index + 1);
          const newX = nodeAPosition.x + Math.cos(newAngle) * distance;
          const newY = nodeAPosition.y + Math.sin(newAngle) * distance;

          if (Math.abs(node.x - newX) > 5 || Math.abs(node.y - newY) > 5) {
            result.movements.push({
              id: node.id,
              x: newX,
              y: newY
            });
          }
        });
      }
    } else {
      //  nodeA 
      const existingChildCount = nodeA.childNodesInfo?.length || 0;
      const angle = (Math.PI / (existingChildCount + 2)) * (existingChildCount + 1);

      previewX = nodeAPosition.x + Math.cos(angle) * distance;
      previewY = nodeAPosition.y + Math.sin(angle) * distance;
      lineColor = [0.98, 0.75, 0.14, 0.8]; // 

      // 
      if (existingChildCount > 0 && currentLayout.nodes) {
        const childNodes = currentLayout.nodes.filter(n => n.type === 'child');
        const totalChildren = existingChildCount + 1;

        childNodes.forEach((node, index) => {
          const newAngle = (Math.PI / (totalChildren + 1)) * (index + 1);
          const newX = nodeAPosition.x + Math.cos(newAngle) * distance;
          const newY = nodeAPosition.y + Math.sin(newAngle) * distance;

          if (Math.abs(node.x - newX) > 5 || Math.abs(node.y - newY) > 5) {
            result.movements.push({
              id: node.id,
              x: newX,
              y: newY
            });
          }
        });
      }
    }

    // 
    result.previewNode = {
      x: previewX,
      y: previewY,
      radius: previewRadius,
      scale: 1,
      opacity: 0.75,
      label: newNodeData.name || '',
      visible: true
    };

    //  nodeA 
    const nodeAId = currentLayout.nodes.find(n => n.data && n.data._id === nodeA._id)?.id;
    if (nodeAId) {
      result.previewLines.push({
        from: nodeAId,
        to: 'preview-new-node',
        color: lineColor,
        isDashed: true,
        isNew: true
      });
    }

    return result;
  }

  /**
   *  A  B 
   */
  calculateInsertPreviewLayout(newNodeData, nodeA, nodeBConfig, currentLayout, nodeAPosition) {
    const nodeB = nodeBConfig.node;
    const direction = nodeBConfig.direction; // 'aToB'  'bToA'

    const result = {
      movements: [],
      previewNode: null,
      previewLines: []
    };

    //  nodeB 
    const nodeBInLayout = currentLayout.nodes.find(
      n => n.data && n.data._id === nodeB._id
    );

    if (!nodeBInLayout) {
      //  nodeB
      return this.calculateSimplePreviewLayout(
        newNodeData,
        nodeA,
        direction === 'aToB' ? 'contains' : 'extends',
        currentLayout,
        nodeAPosition
      );
    }

    const nodeBPosition = { x: nodeBInLayout.x, y: nodeBInLayout.y };

    //  A  B 
    const midX = (nodeAPosition.x + nodeBPosition.x) / 2;
    const midY = (nodeAPosition.y + nodeBPosition.y) / 2;

    // 
    const dx = nodeBPosition.x - nodeAPosition.x;
    const dy = nodeBPosition.y - nodeAPosition.y;
    const perpX = -dy * 0.15; // 
    const perpY = dx * 0.15;

    const previewX = midX + perpX;
    const previewY = midY + perpY;

    // 
    result.previewNode = {
      x: previewX,
      y: previewY,
      radius: 45,
      scale: 1,
      opacity: 0.75,
      label: newNodeData.name || '',
      visible: true
    };

    //  ID
    const nodeALayoutNode = currentLayout.nodes.find(n => n.data && n.data._id === nodeA._id);
    const nodeAId = nodeALayoutNode?.id;
    const nodeBId = nodeBInLayout.id;

    if (!nodeAId || !nodeBId) {
      return result;
    }

    // 
    // aToB:  A B 
    // bToA:  B A 
    const parentLineColor = [0.06, 0.73, 0.51, 0.8]; // 
    const childLineColor = [0.98, 0.75, 0.14, 0.8];  // 

    if (direction === 'aToB') {
      //  A  -> A 
      //  B  ->  B 
      result.previewLines.push({
        from: nodeAId,
        to: 'preview-new-node',
        color: childLineColor,
        isDashed: true,
        isNew: true
      });
      result.previewLines.push({
        from: 'preview-new-node',
        to: nodeBId,
        color: parentLineColor,
        isDashed: true,
        isNew: true
      });
    } else {
      //  B  -> B 
      //  A  ->  A 
      result.previewLines.push({
        from: nodeBId,
        to: 'preview-new-node',
        color: childLineColor,
        isDashed: true,
        isNew: true
      });
      result.previewLines.push({
        from: 'preview-new-node',
        to: nodeAId,
        color: parentLineColor,
        isDashed: true,
        isNew: true
      });
    }

    //  A-B 
    const existingLine = currentLayout.lines.find(
      l => (l.from === nodeAId && l.to === nodeBId) || (l.from === nodeBId && l.to === nodeAId)
    );

    if (existingLine) {
      result.previewLines.push({
        from: existingLine.from,
        to: existingLine.to,
        color: existingLine.color,
        isRemoved: true
      });
    }

    // 
    // 
    const pushDistance = 30;
    for (const node of currentLayout.nodes) {
      if (node.id === nodeAId || node.id === nodeBId) continue;
      if (!node.visible) continue;

      const distToPreview = Math.sqrt(
        Math.pow(node.x - previewX, 2) + Math.pow(node.y - previewY, 2)
      );

      // 
      if (distToPreview < 100) {
        const angle = Math.atan2(node.y - previewY, node.x - previewX);
        result.movements.push({
          id: node.id,
          x: node.x + Math.cos(angle) * pushDistance,
          y: node.y + Math.sin(angle) * pushDistance
        });
      }
    }

    return result;
  }

  /**
   * 
   * @param {Object} nodeA - A
   * @returns {Object} { parents: [], children: [] }
   */
  getCandidateNodesForInsertion(nodeA) {
    //  nodeA 
    return {
      parents: nodeA.parentNodesInfo || [],
      children: nodeA.childNodesInfo || []
    };
  }
}

export default LayoutManager;
