import React, { useState, useEffect } from 'react';
import { User, Camera, Lock, Save, X, Check } from 'lucide-react';
import './ProfilePanel.css';

// 
import defaultMale1 from '../../assets/avatars/default_male_1.svg';
import defaultMale2 from '../../assets/avatars/default_male_2.svg';
import defaultMale3 from '../../assets/avatars/default_male_3.svg';
import defaultFemale1 from '../../assets/avatars/default_female_1.svg';
import defaultFemale2 from '../../assets/avatars/default_female_2.svg';
import defaultFemale3 from '../../assets/avatars/default_female_3.svg';

// 
const avatarMap = {
    default_male_1: defaultMale1,
    default_male_2: defaultMale2,
    default_male_3: defaultMale3,
    default_female_1: defaultFemale1,
    default_female_2: defaultFemale2,
    default_female_3: defaultFemale3
};

// 
const maleAvatars = [
    { id: 'default_male_1', src: defaultMale1, label: '' },
    { id: 'default_male_2', src: defaultMale2, label: '' },
    { id: 'default_male_3', src: defaultMale3, label: '' }
];

const femaleAvatars = [
    { id: 'default_female_1', src: defaultFemale1, label: '' },
    { id: 'default_female_2', src: defaultFemale2, label: '' },
    { id: 'default_female_3', src: defaultFemale3, label: '' }
];

const ProfilePanel = ({ username, onAvatarChange }) => {
    const [userInfo, setUserInfo] = useState(null);
    const [loading, setLoading] = useState(true);
    const [activeTab, setActiveTab] = useState('info'); // 'info', 'avatar', 'password'

    // 
    const [selectedAvatar, setSelectedAvatar] = useState(null);
    const [savingAvatar, setSavingAvatar] = useState(false);

    // 
    const [passwordForm, setPasswordForm] = useState({
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
    });
    const [passwordError, setPasswordError] = useState('');
    const [passwordSuccess, setPasswordSuccess] = useState('');
    const [savingPassword, setSavingPassword] = useState(false);

    // 
    useEffect(() => {
        fetchUserInfo();
    }, []);

    const fetchUserInfo = async () => {
        const token = localStorage.getItem('token');
        try {
            const response = await fetch('http://localhost:5000/api/profile', {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });
            if (response.ok) {
                const data = await response.json();
                setUserInfo(data);
                setSelectedAvatar(data.avatar);
            }
        } catch (error) {
            console.error(':', error);
        } finally {
            setLoading(false);
        }
    };

    // 
    const handleSaveAvatar = async () => {
        if (!selectedAvatar || selectedAvatar === userInfo?.avatar) return;

        setSavingAvatar(true);
        const token = localStorage.getItem('token');

        try {
            const response = await fetch('http://localhost:5000/api/profile/avatar', {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`
                },
                body: JSON.stringify({ avatar: selectedAvatar })
            });

            if (response.ok) {
                const data = await response.json();
                setUserInfo(prev => ({ ...prev, avatar: data.avatar }));
                localStorage.setItem('userAvatar', data.avatar);
                if (onAvatarChange) {
                    onAvatarChange(data.avatar);
                }
                alert('');
            } else {
                const error = await response.json();
                alert(error.error || '');
            }
        } catch (error) {
            console.error(':', error);
            alert('');
        } finally {
            setSavingAvatar(false);
        }
    };

    // 
    const handleChangePassword = async (e) => {
        e.preventDefault();
        setPasswordError('');
        setPasswordSuccess('');

        // 
        if (!passwordForm.oldPassword || !passwordForm.newPassword || !passwordForm.confirmPassword) {
            setPasswordError('');
            return;
        }

        if (passwordForm.newPassword.length < 6) {
            setPasswordError('6');
            return;
        }

        if (passwordForm.newPassword !== passwordForm.confirmPassword) {
            setPasswordError('');
            return;
        }

        setSavingPassword(true);
        const token = localStorage.getItem('token');

        try {
            const response = await fetch('http://localhost:5000/api/profile/password', {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': `Bearer ${token}`
                },
                body: JSON.stringify({
                    oldPassword: passwordForm.oldPassword,
                    newPassword: passwordForm.newPassword
                })
            });

            if (response.ok) {
                setPasswordSuccess('');
                setPasswordForm({ oldPassword: '', newPassword: '', confirmPassword: '' });
            } else {
                const error = await response.json();
                setPasswordError(error.error || '');
            }
        } catch (error) {
            console.error(':', error);
            setPasswordError('');
        } finally {
            setSavingPassword(false);
        }
    };

    // 
    const formatDate = (dateStr) => {
        if (!dateStr) return '-';
        const date = new Date(dateStr);
        return date.toLocaleDateString('zh-CN', {
            year: 'numeric',
            month: 'long',
            day: 'numeric'
        });
    };

    // 
    const getRoleDisplay = (role) => {
        return role === 'admin' ? '' : '';
    };

    if (loading) {
        return (
            <div className="profile-panel">
                <div className="profile-loading">...</div>
            </div>
        );
    }

    return (
        <div className="profile-panel">
            <div className="profile-header">
                <div className="profile-avatar-display">
                    <img
                        src={avatarMap[userInfo?.avatar] || defaultMale1}
                        alt=""
                        className="profile-avatar-large"
                    />
                </div>
                <div className="profile-header-info">
                    <h2 className="profile-username">{userInfo?.username}</h2>
                    <span className="profile-role">{getRoleDisplay(userInfo?.role)}</span>
                    <span className="profile-profession">{userInfo?.profession}</span>
                </div>
            </div>

            <div className="profile-tabs">
                <button
                    className={`profile-tab ${activeTab === 'info' ? 'active' : ''}`}
                    onClick={() => setActiveTab('info')}
                >
                    <User size={18} />
                    
                </button>
                <button
                    className={`profile-tab ${activeTab === 'avatar' ? 'active' : ''}`}
                    onClick={() => setActiveTab('avatar')}
                >
                    <Camera size={18} />
                    
                </button>
                <button
                    className={`profile-tab ${activeTab === 'password' ? 'active' : ''}`}
                    onClick={() => setActiveTab('password')}
                >
                    <Lock size={18} />
                    
                </button>
            </div>

            <div className="profile-content">
                {/*  */}
                {activeTab === 'info' && (
                    <div className="profile-info-section">
                        <div className="info-grid">
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">{userInfo?.username}</span>
                            </div>
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">Lv.{userInfo?.level ?? 0}</span>
                            </div>
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">{userInfo?.experience || 0}</span>
                            </div>
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">{Number(userInfo?.knowledgeBalance || 0).toFixed(2)}</span>
                            </div>
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">{userInfo?.profession}</span>
                            </div>
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">{userInfo?.location || ''}</span>
                            </div>
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">{userInfo?.ownedNodes?.length || 0} </span>
                            </div>
                            <div className="info-item">
                                <span className="info-label"></span>
                                <span className="info-value">{formatDate(userInfo?.createdAt)}</span>
                            </div>
                        </div>
                    </div>
                )}

                {/*  */}
                {activeTab === 'avatar' && (
                    <div className="profile-avatar-section">
                        <div className="avatar-preview">
                            <img
                                src={avatarMap[selectedAvatar] || defaultMale1}
                                alt=""
                                className="avatar-preview-img"
                            />
                            <span className="avatar-preview-label"></span>
                        </div>

                        <div className="avatar-category">
                            <h4 className="avatar-category-title"></h4>
                            <div className="avatar-grid">
                                {maleAvatars.map(avatar => (
                                    <div
                                        key={avatar.id}
                                        className={`avatar-option ${selectedAvatar === avatar.id ? 'selected' : ''}`}
                                        onClick={() => setSelectedAvatar(avatar.id)}
                                    >
                                        <img src={avatar.src} alt={avatar.label} />
                                        <span className="avatar-label">{avatar.label}</span>
                                        {selectedAvatar === avatar.id && (
                                            <div className="avatar-check">
                                                <Check size={16} />
                                            </div>
                                        )}
                                    </div>
                                ))}
                            </div>
                        </div>

                        <div className="avatar-category">
                            <h4 className="avatar-category-title"></h4>
                            <div className="avatar-grid">
                                {femaleAvatars.map(avatar => (
                                    <div
                                        key={avatar.id}
                                        className={`avatar-option ${selectedAvatar === avatar.id ? 'selected' : ''}`}
                                        onClick={() => setSelectedAvatar(avatar.id)}
                                    >
                                        <img src={avatar.src} alt={avatar.label} />
                                        <span className="avatar-label">{avatar.label}</span>
                                        {selectedAvatar === avatar.id && (
                                            <div className="avatar-check">
                                                <Check size={16} />
                                            </div>
                                        )}
                                    </div>
                                ))}
                            </div>
                        </div>

                        <button
                            className="btn btn-primary save-avatar-btn"
                            onClick={handleSaveAvatar}
                            disabled={savingAvatar || selectedAvatar === userInfo?.avatar}
                        >
                            <Save size={18} />
                            {savingAvatar ? '...' : ''}
                        </button>
                    </div>
                )}

                {/*  */}
                {activeTab === 'password' && (
                    <div className="profile-password-section">
                        <form onSubmit={handleChangePassword} className="password-form">
                            <div className="form-group">
                                <label></label>
                                <input
                                    type="password"
                                    value={passwordForm.oldPassword}
                                    onChange={(e) => setPasswordForm(prev => ({ ...prev, oldPassword: e.target.value }))}
                                    placeholder=""
                                    className="form-input"
                                />
                            </div>
                            <div className="form-group">
                                <label></label>
                                <input
                                    type="password"
                                    value={passwordForm.newPassword}
                                    onChange={(e) => setPasswordForm(prev => ({ ...prev, newPassword: e.target.value }))}
                                    placeholder="6"
                                    className="form-input"
                                />
                            </div>
                            <div className="form-group">
                                <label></label>
                                <input
                                    type="password"
                                    value={passwordForm.confirmPassword}
                                    onChange={(e) => setPasswordForm(prev => ({ ...prev, confirmPassword: e.target.value }))}
                                    placeholder=""
                                    className="form-input"
                                />
                            </div>

                            {passwordError && (
                                <div className="password-error">
                                    <X size={16} />
                                    {passwordError}
                                </div>
                            )}

                            {passwordSuccess && (
                                <div className="password-success">
                                    <Check size={16} />
                                    {passwordSuccess}
                                </div>
                            )}

                            <button
                                type="submit"
                                className="btn btn-primary change-password-btn"
                                disabled={savingPassword}
                            >
                                <Lock size={18} />
                                {savingPassword ? '...' : ''}
                            </button>
                        </form>
                    </div>
                )}
            </div>
        </div>
    );
};

export default ProfilePanel;
